<template>
<ul class="sort">
  <li @click="link(data.id)" v-for="(data,index) in datas" :key="data.id" class="sort-item" :style="{background:`url('http://www.shuqi.com/img/class_${index+1}.jpg')`}">
    <h4 class="sort-item-name">{{data.name}}</h4>
    <p class="sort-item-num">({{data.num}})</p>
  </li>
</ul>
</template>

<script>
  import {mapActions} from "vuex"
    export default {
        name: 'sort',
      methods:{
          link(id){
            this.selectClassId(id);
            this.$router.push(`/booksort/${id}`);
          },
        ...mapActions({
          "selectClassId":"selectClassId"
        })
      },
      props:{
        datas:{
          types:Array,
          default(){
            return [{name:"",num:"",id:""}]
          }
        }
      },
        data() {
            return {
            }
        }
    }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  .sort{
display: flex;
flex-flow: row wrap;
  }
.sort-item{
  width: 50%;
  text-align: center;
  line-height: 1.5;
  padding:.2rem 0;
  color:#fff;
  cursor: pointer;
  background-size: cover;
  .sort-item-name{
    font-size: @font-size-m;
  }
  .sort-item-num{
    font-size: @font-size-s;
  }
}
</style>



