<template>
  <swiper :options="options" :class="newClass">
    <!-- slides -->
    <swiper-slide v-for="slider in sliders" :key="slider.id">
      <div class="swiper-slide-href" @click="selectBookId(slider.id)">
        <div class="swiper-slide-wrap" v-show="!needIntro">
          <img :src="slider.cover" class="swiper-slide-href-img">
        </div>
        <h3 v-if="needIntro" class="swiper-slide-title">{{slider.book}}</h3>
        <p v-if="needIntro" class="swiper-slide-author">{{slider.author}}</p>
        <p v-if="needIntro" class="swiper-slide-intro">{{slider.intro}}</p>
      </div>

    </swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination" v-if="options.pagination"></div>
  </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import {mapMutations} from "vuex"

export default {
  name: 'swi',
  components: {
    swiper,
    swiperSlide
  },
  methods: {
    selectBookId(id) {
      this.set_bookId(id);
      this.$router.push({path: `/bookintro/${id}`});
    },
    ...mapMutations({
      "set_bookId": "SET_BOOKID"
    })
  },
  props: {
    needIntro: {
      type: Boolean,
      default: false,
    },
    newClass: {
      type: String,
      default: ""
    },
    sliders: {
      type: Array,
      default() {
        return [{cover: ""}]
      }
    },
    options: {
      type: Object,
      default() {
        return {
          lazy: false,
          autoplay: false,
          direction: "horizontal",
          loop: true,
          effet: "slide",
          slidesPerView: 1,
          // onProgress:"",
          coverflowEffect:"",
          clickable: true,
         spaceBetween: 0,
         pagination: "",
       }
     }
   }
 },
 data() {
  return {}
}
}
</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

.swiper-slide {
  cursor: pointer;
  .swiper-slide-href {
    display: block;
    text-align: center;
    line-height: 1.5;
    .swiper-slide-wrap {
      background: url("~common/img/logo.png");
      .swiper-slide-href-img {
        display: block;
        width: 100%;
      }
      .swiper-slide-title {
        font-size: @font-size-m;
        .nowrap;

      }
      .swiper-slide-author {
        font-size: @font-size-s;
        .nowrap;
      }
    }
  }
}

.book-shop-top-sliders {
  height: 100%;
  color:#fff;
  overflow: hidden;
  font-size: @font-size-m;
  .swiper-slide-title{
    padding-top: .8rem;
      line-height: 2;
  }
    .swiper-slide-author{
    font-size: @font-size-s;
    color:@color-font-gray;
  }
  .swiper-slide-intro{
    height: 2rem;
    overflow: hidden;
    padding:0 .4rem;
      line-height: 2;
      text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 4;
-webkit-box-orient: vertical;
  font-size: @font-size-m;

  }
  .swiper-slide-href-img{
    display: block;
  }

}


.panel-content {
  .swiper-slide-title {
    font-size: @font-size-m;
    .nowrap;
  }
  .swiper-slide-author {
    font-size: @font-size-s;
    .nowrap;
  }

}

