  /*.swiper-slide{*/
    /*width:auto;*/
    /*.swiper-slide-href{*/
      /*.swiper-slide-href-img{*/
        /*display: block;*/
        /*}*/
        /*}*/

        /*}*/
        /*}*/
        </style>



