import Vue from 'vue'
import Router from 'vue-router'

import BookShelf from 'components/book-shelf'
import BookShop from 'components/book-shop'
import BookSort from 'components/book-sort'
import BookReader from 'components/book-reader'
import BookSearch from 'components/book-search'
import BookIntro from 'components/book-intro'

Vue.use(Router)

export default new Router({
  linkActiveClass:"active",
  // mode:"history",
  routes: [
    {
      path:'*',
      redirect:'/bookshelf'
    },
    {
      path: '/bookshelf',
      component: BookShelf,
      meta:{keepAlive:false}
    },
    {
      path:"/booksort/:id",
      component:BookSort,
      meta:{keepAlive:false},
      children:[
        {
          path:'/booksort/book/:id',
          component:BookIntro,
          meta:{keepAlive:false}
        },
      ]
    },
    {
      path:"/booksearch/:content",
      component:BookSearch,
      meta:{keepAlive:false},
    },
    {
      path:'/bookshop',
      component:BookShop,
      meta:{keepAlive:true},
    },
    {
      path:'/bookintro/:id',
      component:BookIntro,
      meta:{keepAlive:false}
    },
    {
      path:'/bookreader/:id',
      component:BookReader,
      meta:{keepAlive:false}
    },
  ]
})



