export function getComment(authorId,bookId,page=0) {

  let url="https://read.xiaoshuo1-sm.com/novel/i.php",
    data={
      do:"sp_get",
      authorId:authorId,
      bookId:bookId,
      fetch:"merge",
      source:"store",
      sqUid:8000000,
      page:page
    };
  data=formatUrl(data);
  url+=url.indexOf("?")>0?data:("?"+data);
  return axios.get(url);
}



