<template>
  <div class="reader" ref="reader">
    <div class="reader-top" :class="{active:active==1}">
      <i class="icon-fanhui" @click="back"></i>
      <div class=reader-top-more>
        <i class="icon-gengduo-tianchong" @click="isLookMore=!isLookMore"></i>
        <ul v-show="isLookMore" class="more-nav">
          <li class="more-nav-item" @click="toBookShelf"><i class="icon-shujia"></i>去书架</li>
          <li class="more-nav-item" @click="toBookIntro"><i class="icon-book_icon"></i>书籍详情</li>
          <li class="more-nav-item"><i class="icon-fenxiang"></i>分享此书</li>
        </ul>
      </div>
    </div>
    <div class="bg" ref="bg"></div>
    <div class="mask" ref="mask"></div>
    <div class="click-mask" @click="select"></div>
    <p v-if="!content" class="waite">正在加载中...请稍等...</p>
    <div class="content" v-html="content" ref="content" @click="select"></div>
    <div class="page-btn">
      <span class="page-btn-prev" :class="{wrong:index==0}" @click="changePage(-1)">上一章</span>
      <span class="page-btn-next" :class="{wrong:index>=catalog.length-1}" @click="changePage(1)">下一章</span>
    </div>
    <div class="reader-bottom">
      <ul :class="{active:active==1}" class="reader-bottom-nav">
        <li class="reader-bottom-nav-item" @click="changeActive(2)"><i class="icon-sort"></i>目录</li>
        <li class="reader-bottom-nav-item" @click="changeActive(3)"><i class="icon-liangdu"></i>亮度</li>
        <li class="reader-bottom-nav-item" @click="changeActive(4)"><i class="icon-shezhi"></i>设置</li>
        <li class="reader-bottom-nav-item" @click="changeActive(5)"><i class="icon-pinglun"></i><span class="comment-num">{{totalCommentNum}}</span>评论</li>
      </ul>
      <ul class="reader-bottom-content">
        <li class="reader-bottom-content-cata" ref="cata" :class="{active:active==2}">
          <div class="reader-bottom-content-cata-title">
            <h3 class="title">{{book_name}}</h3>
            <h4 class="catalog-num">共{{catalogLength}}章</h4>
            <p class="catalog-sort-set"><i :class="sortClass" @click="sortCatalog" class="icon-sort"></i></p>
          </div>
          <ul class="catalog-list-wrap">
            <li @click="toChapId(item.chapid,index)" class="catalog-list" :class="{active:item.chapid==chapId}"
                ref="cataItems" v-for="(item,index) in catalog" :key="item.chapid">
              {{item.chap_name}}
              <i v-if="item.isVip==1" class="icon-qiandai"></i>
            </li>
          </ul>
        </li>
        <li class="reader-bottom-content-sun" :class="{active:active==3}">
          <i class="icon-liangdu icon-l"></i>
          <i class="icon-liangdu icon-r"></i>
          <div class="ld-line" @click="changeLD" ref="ldBg">
            <p class="ld-adjust" ref="ldLine">
            </p>
          </div>
        </li>
        <li class="reader-bottom-content-set" :class="{active:active==4}">
          <div class="font-size sets">
            <h5 class="title">字号</h5>
            <div class="font-size-set sets-item">
              <p>
                <i class="icon-53 icon-num" @click="changeFontSize(-2)"></i>
                <!--<button @click="changeFontSize(-2)" class="set-font-size">-</button>-->
                <span class="font-size-num">{{fontSize}}</span>
                <i class="icon-jia2 icon-num" @click="changeFontSize(2)"></i>
                <!--<button  class=" set-font-size">+</button>-->
              </p>
            </div>

          </div>
          <div class="font-family sets">
            <h5 class="title">字体</h5>
            <div class="sets-item font-family-set">
              <p>
              <span v-for="(item,index) in fontFamilyset" :style="{fontFamily:item.family}"
                    @click="chooseFont(index)" :class="{active:index==fontFamilyIndex}">{{item.text}}</span>
              </p>
            </div>

          </div>
          <div class="theme-color sets">
            <h5 class="title">主题</h5>
            <div class="theme-color-set sets-item">
              <p>
              <span class="color-set" v-for="(item ,index) in colorset" :class="{active:index==bgIndex}"
                    :key="item.color" :style="{background:item.bg}" @click="changeTheme(index)"></span>
              </p>
            </div>

          </div>
        </li>
        <li class="reader-bottom-content-comment" :class="{active:active==5}">
          <ul>
            <li v-for="(comment,index) in comments" :key="index">
              <img v-lazy="comment.userPhoto">
              <h5>{{comment.nickName}}</h5>
              <p>{{comment.text}}</p>
            </li>
            <li class="download-comments"v-if="!commentLoaded" @click="lookMoreComments">
              继续加载请点击...
            </li>
            <li v-else>已加载完毕...</li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {getChap, getComment} from "api/bookreader"
  import {mapGetters, mapMutations} from "vuex"
  import {ERR_OK, ERR_MONEY} from "common/js/config"
  import {getCatalog} from "api/bookintro"
  import {addBooks} from "common/js/storage"
  import {
    getLight,
    setLight,
    getFontSize,
    setFontSize,
    getTheme,
    setTheme,
    getFontFamily,
    setFontFamily
  } from "common/js/storage"

  export default {
    name: 'reader',
    created() {
      if (!this.bookId) {
        this.$router.back();
        return
      }
    },
    mounted() {
      this._getChap();
      this._getCatalog();
      let n = getLight();
      this.domAction(n);
      this.chooseFont();
      this.changeFontSize();
      this.changeTheme();
    },
    methods: {
      lookMoreComments(){
        this.commentPage++;
        this._getComment();
      },
      _getComment() {
        getComment(this.sqAuthorId, this.sqBid,this.commentPage).then(res => {
          if (res.status == ERR_OK) {
            let data = res.data.data;
            this.totalCommentNum=res.data.info.total;
            this.comments = this.comments.concat(data)
          }
        })
      },
      toBookShelf() {
        this.$router.push({path: `/bookshelf`})
      },
      toBookIntro() {
        this.$router.push({path: `/bookIntro/${this.bookId}`})
      },
      chooseFont(index = getFontFamily()) {
        this.$refs.content.style.fontFamily = this.fontFamilyset[index]["family"];

        this.fontFamilyIndex = index;
        setFontFamily(index);
      },
      changeFontSize(num) {
        num = num == undefined ? getFontSize() : (num + parseInt(getFontSize()));
        num = num < 12 ? 12 : num > 40 ? 40 : num;
        this.$refs.content.style.fontSize = num / 100 + "rem";
        this.fontSize = num;

        setFontSize(num);
      },
      changeTheme(index = getTheme()) {
        this.$refs.bg.style.background = this.colorset[index]["bg"];
        this.$refs.content.style.color = this.colorset[index]["color"];
        this.bgIndex = index;
        setTheme(index);
      },
      toChapId(chapId, index) {
        this.index = index;
        this.set_chapId(chapId);
        this._getChap();
        this.active = 0;
        addBooks(this.bookId, chapId)
        this.$refs.reader.scrollTop = 0;
      },
      back() {
        this.$router.back();
      },
      domAction(n) {
        let w = this.$refs.ldBg.offsetWidth;
        this.$refs.ldLine.style.width = n / .3 * w + "px";
        this.$refs.mask.style.opacity = n;
      },
      changePage(num) {
        this.index = this.index + num;
        if (this.index < 0) {
          this.index = 0;
          return;
        } else if (this.index >= this.catalog.length) {
          this.index = this.catalog.length - 1;
          return;
        }
        let chapId = this.catalog[this.index].chapid
        this.set_chapId(chapId);
//        this.content = "";
        this._getChap();
        addBooks(this.bookId, chapId)
        this.$refs.reader.scrollTop = 0;
      },
      _getChap() {

        getChap(this.bookId, this.chapId).then(res => {
          if (res.status == ERR_OK) {
            if (res.data.state == ERR_MONEY) {
              this.content = "这个章节要钱啊";
              return;
            }
            if (!res.data) {
              this.back()
            }
            let data = res.data.data.content;
            data = data.replace(/#+/g, "。");
            data = data.replace(/\n/g, "</p><p>");
            data = data.slice(0, -1);
            data = "<p>" + data + "</p>";
            this.content = data;
            addBooks(this.bookId, this.chapId)


          }
        })
      },
      _getCatalog() {
        getCatalog(this.bookId).then(res => {
          if (res.status == ERR_OK) {
            this.catalog = [];
            this.comments=[];
            this.commentPage=1;
            this.book_name = res.data.main.book_name;
            this.sqAuthorId = res.data.main.sqAuthorId;
            this.sqBid = res.data.main.sqBid;
            this._getComment();
            let data = res.data.data;
            if (data) {
              data.forEach(item => {
                item.volumeList.chapters.forEach((i, index) => {
                  if (this.chapId == i.chapid) {
                    this.index = index;
                  }
                  i.chap_name = i.chap_name.replace(/\s+/g, "");
                  this.catalog = this.catalog.concat(i);
                })
              })
            }


          }
        })
      },
      select() {
        if (this.active != 0) {
          this.active = 0;
          return;
        }
        this.active = 1;
      },
      changeActive(num) {
        this.active = num;
        this.$refs.mask.style.opacity = getLight();
        this.$refs.cata.scrollTop = this.$refs.cataItems[this.index].offsetTop;
      },
      changeLD(e) {
        let w = this.$refs.ldBg.offsetWidth,
          curL = e.clientX - this.$refs.ldBg.offsetLeft,
          n = curL / w * .3;
        this.domAction(n);
        setLight(n)
      },
      ...mapMutations({
        "set_chapId": "SET_CHAPID"
      }),
      sortCatalog() {
        this.cataSort = !this.cataSort;
        this.catalog = this.catalog.reverse();
      },
    },
    computed: {
      commentLoaded(){
        return this.commentPage*10>=this.totalCommentNum;
      },
      catalogLength() {
        return this.catalog.length;
      },
      sortClass() {
        if (this.cataSort) {
          return "icon-paixu-sheng";
        } else {
          return "icon-paixu-jiang";
        }
      },
      ...mapGetters([
        "chapId",
        "bookId"
      ])
    },
    data() {
      return {
        isLookMore: false,
        content: "",
        catalog: [],
        beginCatalog: "",
        book_name: "",
        active: 0,
        index: 0,
        sqAuthorId: "",
        sqBid: "",
        totalCommentNum:"",
        commentPage:"",
        comments: [],
        fontSize: 18,
        colorset: [
          {color: "#333", bg: "#FDF6EE"},
          {color: "#15695E", bg: "#A9EED6"},
          {color: "#5F526A", bg: "#E4C8F6"},
          {color: "#52606C", bg: "#CCEFFE"},
          {color: "#424242", bg: "#FFF"},
          {color: "#737C55", bg: "#F5FFB3"},
          {color: "#675555", bg: "#FDD9D9"},
          {color: "#254D50", bg: "url(./static/bg-theme.jpg)"},
          {color: "#1E573C", bg: "url(./static/bg-theme2)"},
        ],
        fontFamilyset: [
          {family: "Microsoft YaHei", text: "默认字体"},
          {family: "SimHei", text: "黑体"},
          {family: "KaiTi", text: "楷体"},
          {family: "YouYuan", text: "幼圆"},
          {family: "FZShuTi", text: "方正舒体"},
          {family: "SThupo", text: "华文琥珀"},
        ],
        bgIndex: 0,
        fontFamilyIndex: 0,
        cataSort: true,

      }
    }
  }
</script>

<style lang="less" type="text/less">
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .transform {
    transition: all .3s ease-in;
    opacity: 0.3;
  }

  .active {
    transform: translateY(0) translateX(0);
    opacity: 1;
  }

  .active2 {
    transform: translateY(-100%);
    opacity: 1
  }

  .waite {
    font-size: @font-size-m;
    position: relative;
    z-index: 5559;
    color: @color-theme;
    line-height: 10;
    text-align: center;
  }

  .reader {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    z-index: 6000;

    .reader-top {
      position: fixed;
      left: 0;
      top: 0;
      height: .7rem;
      width: 100%;
      background-color: @color-bg-gray2;
      z-index: 60;
      transform: translateY(-100%);
      .transform;
      &.active {
        .active;
      }
      .icon-fanhui {
        position: absolute;
        left: .1rem;
        top: 0;
        line-height: .6rem;
      }
      .reader-top-more {
        position: absolute;
        right: .2rem;
        top: 0;

        .icon-gengduo-tianchong {
          position: absolute;
          right: .2rem;
          top: 0;
          line-height: .6rem;
        }
        .more-nav {
          background-color: @color-bg-white;
          position: absolute;
          right: 0;
          top: .6rem;
          font-size: @font-size-s;
          white-space: nowrap;
          line-height: 2;
          border: 1px solid @color-line;
          .more-nav-item {
            position: relative;
            padding-left: .5rem;
            padding-right: .5rem;
            line-height: .6rem;
            user-select: none;

            i {
              position: absolute;
              line-height: .6rem;
              left: .1rem;
              top: 0;
            }
          }

        }
      }
    }
    .reader-bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 0;
      z-index: 55;
      .reader-bottom-nav {
        background-color: @color-bg-gray2;
        height: .8rem;
        .transform;
        display: flex;
        flex-flow: row nowrap;
        font-size: @font-size-s;
        &.active {
          .active2
        }
        .reader-bottom-nav-item {
          flex: 1;
          text-align: center;
          padding-top: .55rem;
          position: relative;
          i {
            font-size: @font-size-l;
            position: absolute;
            margin-left: -@font-size-l/2;
            left: 50%;
            top: .15rem;
          }
        }
      }
      .reader-bottom-content {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        .reader-bottom-content-cata {
          position: fixed;
          width: 80%;
          z-index: 222;
          top: 0;
          bottom: 0;
          overflow: auto;
          box-shadow: 8px 8px 30px @color-theme-font;
          background-color: @color-bg-th;
          line-height: 1.5;
          padding: .2rem;
          box-sizing: border-box;
          transform: translateX(-100%);
          .transform;
          &.active {
            .active;
          }
          .reader-bottom-content-cata-title {
            position: fixed;
            top: 0;
            left: 0;
            font-size: @font-size-m;
            padding: .1rem .2rem;
            width: 100%;
            box-sizing: border-box;
            background-color: @color-bg-th;
            .title {
              font-size: @font-size-m;
              .nowrap;
            }
            .catalog-num {
              font-size: @font-size-xs;
            }

          }
          .catalog-list-wrap {
            padding: .3rem 0;
            margin-top: .8rem;
            .catalog-list {
              font-size: @font-size-m;
              line-height: 1.5;
              cursor: pointer;
              .nowrap;
              &.active {
                color: @color-text-danger;
              }
            }
            position: relative;
            .icon-qiandai {
              position: absolute;
              font-size: @font-size-s;
              right: 0;
              color: @color-theme;
            }
          }

        }
        .reader-bottom-content-sun {
          background-color: @color-bg-gray2;
          position: fixed;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1rem;
          transform: translateY(100%);
          .transform;
          &.active {
            .active;
          }
          .icon-liangdu {
            position: absolute;
            font-size: @font-size-l;
            top: 50%;
            margin-top: -@font-size-l/2;
            &.icon-l {
              left: .15rem;
            }
            &.icon-r {
              right: .15rem;
              color: @color-theme;
            }
          }
          .ld-line {
            margin: .45rem .7rem;
            background: @color-font-gray;
            border-radius: .1rem;
            height: .1rem;
            position: relative;
            .ld-adjust {
              width: 0;
              background-color: @color-theme;
              border-radius: .1rem;
              height: .1rem;
              position: relative;
              &::before, &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                width: .2rem;
                height: .2rem;
                border-radius: 50%;
                transform: translateY(-50%) translateX(50%);
              }
              &::before {
                background-color: @color-bg-white;
              }
              &::after {
                background-color: @color-theme;
                width: .1rem;
                height: .1rem;

              }

            }
          }

        }
        .reader-bottom-content-set {
          position: absolute;
          transform: translateY(100%);
          left: 0;
          bottom: 0;
          right: 0;
          width: 100%;
          box-sizing: border-box;
          padding: .2rem;
          opacity: .2;
          .transform;
          &.active {
            .active;
          }
          background-color: @color-bg-gray;
          box-shadow: 0 -10px 30px @color-font-black;
          font-size: @font-size-s;
          .theme-color-set {
            white-space: nowrap;

            .color-set {
              display: inline-block;
              margin-right: .2rem;
              margin-left: .05rem;
              line-height: 1rem;
              margin-top: .2rem;
              width: .5rem;
              height: .5rem;
              border-radius: 50%;
              background-size: cover;
              background-repeat: no-repeat;
              position: relative;
              &.active {
                transform: scale(.8);
                &::after {
                  position: absolute;
                  content: "";
                  width: .7rem;
                  left: 50%;
                  top: 50%;
                  transform: translateY(-50%) translateX(-50%);
                  height: .7rem;
                  border: 1px solid @color-theme;
                  border-radius: 50%;
                }
              }
            }
          }
          .sets {
            position: relative;
            padding-left: .9rem;
            height: 1rem;
            line-height: 1rem;
            font-size: @font-size-l;

            .title {
              position: absolute;
              left: .2rem;
              top: 0;
              color: @color-font-gray;
            }
            .icon-num {
              cursor: pointer;
              font-size: @font-size-xxl+.23;
              color: @color-theme;
            }
            .icon-53 {
              font-size: @font-size-xxl+.3;
            }
            .font-size-num {
              margin: 0 .3rem;
            }
            .sets-item {
              position: absolute;
              height: 1rem;
              white-space: nowrap;
              line-height: 1rem;
              overflow-x: auto;
              p {
                height: 1rem;
                width: 5rem;
              }

            }
            .font-family-set {
              span {
                margin-right: .2rem;
                cursor: pointer;
                font-size: @font-size-m;
                &.active {
                  color: @color-theme;
                  font-size: @font-size-l;
                }
              }
            }

          }

        }

      }
    }
    .catalog-sort-set {
      width: 100%;
      margin-bottom: .2rem;
      position: relative;
      .icon-sort {
        float: right;
        cursor: pointer;
        font-size: @font-size-l;

      }
    }

    .content {
      user-select: none;
      padding: .1rem;
      position: relative;
      z-index: 55;

      p {
        line-height: 1.5;
        margin: .1rem 0;
      }
    }
    .bg, .mask, .click-mask {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: #000;
      z-index: 0;
      opacity: 0;
    }
    .bg {
      background-color: @color-bg-th;
      opacity: 1;
    }
    .page-btn {
      font-size: @font-size-s;
      text-align: center;
      position: relative;
      z-index: 55;
      .page-btn-prev, .page-btn-next {
        display: inline-block;
        width: 40%;
        line-height: 2;
        cursor: pointer;
        color: @color-theme;
        border-radius: .2rem;
        border: 1px solid @color-theme;
        margin-bottom: .15rem;

      }
      .wrong {
        color: @color-font-gray;
        border-color: @color-font-gray;
      }
    }
    .reader-bottom-content-comment {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 5rem;
      overflow-y: auto;
      line-height: 2;
      background-color: @color-bg-gray;
      transform: translateY(100%);
      .transform;
      &.active{
        .active;
      }
      li{
        position: relative;
        padding:.15rem;
        padding-left: 1.2rem;
        font-size: @font-size-m;
        img{
          position: absolute;
          width: .8rem;
          height: .8rem;
          left: .2rem;
          border-radius: 50%;
        }
        h5{
          font-size: @font-size-l;
        }
      }

    }
    .download-comments{
      cursor: pointer;
    }
    .comment-num{
      position: absolute;
      left: 53%;
      top: 0.15rem;
      color:@color-theme;
      background-color: @color-bg-gray;
      border-radius: 50%;
    }
  }
</style>



