<template>
  <div class="book-intro">
    <div class="book-intro-top">
      <img v-lazy="cover" class="book-intro-top-bg">
      <i class="icon-fanhui" @click="toBack"></i>
      <div class="book-intro-top-content">
        <img v-lazy="cover" class="book-intro-top-content-img">
        <h4 class="book-intro-top-content-book">{{book_name}}</h4>
        <h5 class="book-intro-top-content-author">{{author_name}}</h5>
        <p class="book-intro-top-content-message">{{class_name}} {{size}}字 {{end == 1 ? "连载中" : "完结"}}</p>
      </div>
      <p class="book-intro-top-intro">{{intro}}</p>
    </div>
    <div class="line"></div>
    <ul class="book-intro-nav">
      <li><span><i class="icon-tubiaozuixin"></i>最新</span><p>{{newChapName}}</p></li>
      <li @click="lookCatalog=!lookCatalog"><span > <i class="icon-sort"></i>目录</span></li>
      <li v-if="!author_name">加载中...请稍等</li>
    </ul>
    <catalog v-show="lookCatalog" @startRead="toRead"></catalog>
    <div class="line"></div>
    <ul class="bottom-actions">
      <li ref="actionL" @click="addBookToShelf" class="bottom-actions-btn bottom-actions-btn-l">{{btnL}}</li>
      <li ref="actionR" @click="startRead" class="bottom-actions-btn bottom-actions-btn-r">{{btnR}}</li>
    </ul>
    <div class="seat"></div>
  </div>
</template>

<script>
  import {getBookIntro, getComment} from "api/bookintro"
  import {mapGetters, mapMutations} from "vuex"
  import {ERR_OK} from "../../common/js/config";
  import Catalog from "components/book-catalog"
  import {getTime} from "common/js/js"
  import {hasBooks, addBooks, getBooks} from "common/js/storage"

  export default {
    name: 'book-intro',
    components: {
      Catalog
    },
    created() {
      if (!this.bookId) {
        this.toBack();
        return;
      }
    },
    mounted(){
      this._getBookIntro();
      this._inits();
    },
    methods: {
      _inits() {
        if (hasBooks(this.bookId)) {
          this.btnL = "已添加";
          this.btnR = "继续阅读";
          return;
        }
        this.btnL = "加入书架";
        this.btnR = "开始阅读";
      },
      _addBook(val=this.firstChapid ) {
        let obj = {};
        obj.id = this.id;
        obj.title = this.book_name;
        obj.cid = val;
        obj.cover=this.cover;
        obj.end = this.end;
        obj.newc = this.newChapName;
        obj.time = getTime();
        obj.yidu = this.firstChapid;
        addBooks(obj);
        this.set_chapId(val)
        this._inits();
      },
      addBookToShelf(){
        if (!hasBooks(this.bookId)) {
          this._addBook();
        }
      },
      startRead() {
        if (!hasBooks(this.bookId)) {
          this._addBook();
          this.$router.push({path: `/bookreader/${this.bookId}`});
          return;
        }
        this.set_chapId(getBooks(this.bookId).cid);
        this.$router.push({path: `/bookreader/${this.bookId}`})
      },
      toRead(val) {
        this._addBook(val)
        this.$router.push({path: `/bookreader/${this.bookId}`})
      },
      toBack() {
        this.$router.back();
      },
      _getBookIntro() {
        getBookIntro(this.bookId).then(res => {
          if (res.status === ERR_OK) {
            let data = res.data.data;
            let like = res.data.like;
            this.author_name = data.author_name;
            this.id = data.bid;
            this.book_gender = data.book_gender;
            this.book_name = data.book_name;
            this.class_name = data.class_name;
            this.cover = data.cover;
            this.authorId = data.sqAuthorId,
              this.sqBid = data.sqBid,
              this.intro = data.intro;
            this.end = data.end;
            this.newChapContent = data.newChapContent;
            this.newChapName = data.newChapName;
            this.firstChapid = data.fristChapid;
            this.size = data.size;
            this.up_time = data.up_time;
            this.like = like;
          }
        })
      },
      ...mapMutations({
        "set_chapId": "SET_CHAPID"
      })
    },
    computed: {
      ...mapGetters([
        "bookId"
      ])
    },
    data() {
      return {
        author_name: "",
        id: "",
        book_name: "",
        class_name: "",
        size: "",
        sqBid: "",
        intro: "",
        newChapName: "",
        newChapContent: "",
        newChapid: "",
        end: "",
        cover: "",
        like: [],
        firstChapid: "",
        page: "",
        authorId: "",
        lookCatalog: false,
        btnL: "加入书架",
        btnR: "开始阅读",

      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .book-intro {
    position: relative;
    z-index: 5000;
    background-color: @color-bg-white;
    .book-intro-top {
      padding: .2rem;
      position: relative;
      .book-intro-top-bg {
        position: absolute;
        width: 95%;
        height: 50%;
        z-index: -1;
        filter: blur(40px);
      }
      .icon-fanhui {
        display: block;
        color: @color-theme;
        margin-bottom: .2rem;
        font-size: @font-size-m;
        cursor: pointer;
      }
      .book-intro-top-content {
        text-align: center;
        line-height: 2;
        .book-intro-top-content-img {
          width: 30%;
          display: inline-block;
          margin-bottom: .1rem;
        }
        .book-intro-top-content-book {
          font-size: @font-size-l;
        }
        .book-intro-top-content-author {
          font-size: @font-size-s;
        }
        .book-intro-top-content-message {
          font-size: @font-size-s;
          color: @color-font-ligth-white;
        }
      }
      .book-intro-top-intro {
        font-size: @font-size-s;
        text-indent: 2em;
        color: @color-font-ligth-white;
        line-height: 1.2;
      }
    }
    .line {
      height: .1rem;
      background-color: @color-bg-gray;
    }
    .book-intro-nav {
      cursor: pointer;
      li {
        height: 1rem;
        font-size: @font-size-m;
        position: relative;
        padding-left: .3rem;
        p {
          line-height: 1rem;
          font-size: @font-size-m;
          .nowrap;
        }
      ;
        span {
          float: left;
          line-height: 1rem;
          margin-right:.3rem;
          i {

            float: left;

            margin-right: .1rem
          }
        }
        &::after {
          content: '';
          position: absolute;
          width: 100%;
          bottom: 0;
          left: 0;
          height: 1px;
          background-color: @color-line;
          /*transform: scaleY(.4);*/
        }
        .nowrap;

      }}
    .bottom-actions {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: .7rem;
      background-color: @color-bg-white;
      .bottom-actions-btn {
        width: 50%;
        float: left;
        line-height: .7rem;
        text-align: center;
        cursor: pointer;
        font-size: @font-size-m;
        &.bottom-actions-btn-l {
          color: @color-theme;
        }
        &.bottom-actions-btn-r {
          background-color: @color-theme;
          color: @color-font-white;

        }
      }
    }
    .seat {
      height: .97rem;
    }
  }
</style>



