<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <tab></tab>
  </div>
</template>

<script>
  import Tab from "components/tab"
export default {
  name: 'App',
  components:{
    Tab
  }
}
</script>

<style type="text/less" lang="less">
  @import "~common/less/index";
  #app{
    max-width: 800px;
    margin:0 auto;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    .seat{
      height:.97rem;
    }
  }

</style>



