export function getTime(){
  let time=((new Date).getTime() + "").substring(0, 10);
  return time;
}
export function getBJtime(val){
	val+="000";

	let time=new Date(parseInt(val));
	let y=time.getFullYear();
	let m=time.getMonth()+1;
	let d=time.getDate();
	m=m<10?"0"+m:m;
	d=d<10?"0"+d:d
	return y+"-"+m+"-"+d;

}


