<template>
  <div class="shelf">
    <div class="shelf-top">
      <img v-if="books[0]" v-lazy="books[0].cover" class="newest-book-img">
      <h3 class="newest-book-title" v-if="books[0]">{{books[0].title}}</h3>
      <span class="newest-book-action" v-if="books[0]">继续阅读</span>
    </div>
    <ul class="book-list">
      <li v-for="book in books" class="book-list-item" >

        <div class="book-display" @touchstart.stop="noteStart" @touchend.stop="deleteBook">
          <img v-lazy="book.cover" class="book-list-item-img" >
          <div class="img-mask" :data-cid="book.cid" @click="toReader(book.id,book.cid)"></div>

        </div>
        <div class="book-list-item-delete" v-show="readyDelete==book.cid" @click.stop>
          <i class="icon-unredo icon" @click.stop="readyDelete=''"></i>
          <i class="icon-remove-1-copy icon" @click.stop="deleteBookE(book.cid)"></i>
        </div>
        <h4 class="book-list-item-title">{{book.title}}</h4>
      </li>
      <li class="book-list-item book-list-item-display" @click="toShop">
        <i class="icon-jia"></i>
      </li>
    </ul>
  </div>
</template>

<script>
  import {getBooks, removeBooks} from "common/js/storage"
  import {mapMutations, mapGetters} from "vuex"

  export default {
    name: 'shelf',
    created() {
      this._getBooks();
    },
    methods: {
      deleteBookE(id) {
        removeBooks(id);
        this.readyDelete="";
        this._getBooks();
      },
      deleteBook(e) {
        e = e || window.event;
        let n = parseInt(e.timeStamp - this.timeStamp);
        let target = e.target || e.srcElement;
        this.readyDelete = n > 700 ? target.dataset.cid : "";
        console.log(this.readyDelete,0, target.dataset.cid);


      },
      noteStart(e) {
        this.timeStamp = e.timeStamp;

      },
      toReader(bookid, chapid) {
        console.log(1);
        this.set_bookid(bookid);
        this.set_chapid(chapid);
        this.$router.push({path: `/bookreader/${chapid}`})
      },
      toShop() {
        this.$router.push({path: "/bookshop"})
      },
      _getBooks() {
        let ary = getBooks();
        if (ary) {
          ary.sort((a, b) => {
            return b.time - a.time
          });
          this.books = ary;
          return
        }
        this.books = [{cover: "", title: "", cid: ""}]
      },
      ...mapMutations({
        "set_bookid": "SET_BOOKID",
        "set_chapid": "SET_CHAPID",
      })
    },
    watch: {
      bookId(neww, old) {
        this._getBooks();
      }
    },
    computed: {
      ...mapGetters([
        "bookId"
      ])
    },
    data() {
      return {
        books: [],
        readyDelete: "",
        timeStamp: 0,
      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .shelf {
    font-size: @font-size-s;
    line-height: 2;
    overflow: hidden;
    margin-bottom: .6rem;
    .shelf-top {
      height: 3rem;
      background: url("~common/img/shelf.jpg") no-repeat;
      background-size: cover;
      padding-left: 2.5rem;
      position: relative;
      color: @color-font-white;
      .newest-book-img {
        position: absolute;
        left: .5rem;
        top: .4rem;
        width: 1.8rem;
      }
      .newest-book-title {
        font-size: @font-size-m;
        padding-top: .5rem;
      }
      .newest-book-action {
        font-size: @font-size-xs;
        cursor: pointer;
      }
    }
    .book-list {
      padding: .15rem;
      margin-left: -3%;
      margin-bottom: .6rem;
      .book-list-item {
        width: 30%;
        position: relative;
        float: left;
        margin-bottom: .2rem;
        margin-left: 3%;

        .book-display {
          height: 2.6rem;
          overflow: hidden;
          position: relative;
          &:hover {
            .book-list-item-img {
              transform: scale(1.3);
            }
          }

          .book-list-item-img,{
            width: 100%;
            display: block;

          }
          .img-mask{
            .book-list-item-img;
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            user-select: none;
          }
        }

        .book-list-item-title {
          text-align: center;
          .nowrap;
        }
        &.book-list-item-display {
          background-color: rgba(0, 0, 0, .3);
          height: 2.6rem;
          position: relative;
          .icon-jia {
  
            font-size: @font-size-l;
            position: absolute;
            left: 50%;
            top: 50%;
            &::before {
              transform: translateY(-50%) translateX(-50%);
              position: absolute;
              left: 0;
              top: 0;
            }
          }
        }
      }
    }
    .book-list-item-delete {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 2.6rem;
      overflow: hidden;
      background-color: rgba(0, 0, 0, .7);
      color: @color-font-white;
      .icon {
        font-size: @font-size-xxl;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%);
        &.icon-unredo {
          transform: translateY(-100%) translateX(-50%);
        }
      }
    }
  }
</style>



