<template>
  <ul class="books">
    <li v-for="book in datas" :key="book.book_name" class="books-item" @click="setBookId(book.id)">
      <img v-lazy="book.cover">
      <h3 v-html="book.book_name"></h3>
      <h4>作者:{{book.author}}</h4>
      <div class="intro">
        <p>{{book.class_name}}/{{book.end == 1 ? "连载中" : "完结"}}/{{book.size}}字</p>
        <p>最新章节:{{book.newChapName}}</p>
        <p>更新时间:{{book.up_time}}</p>
      </div>
    </li>
  </ul>
</template>

<script>
import {mapMutations} from "vuex"
    export default {
        name: 'bookList',
      props:{
          datas:{
            type:Array,
            default(){
              return[]
            }
          }
      },
      methods:{
          setBookId(id){
            this.set_bookId(id);
              this.$router.push({path:`/bookintro/${id}`});
          },
        ...mapMutations({
          "set_bookId":"SET_BOOKID"
        })
      },
        data() {
            return {}
        }
    }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";
  .books {
    margin: .2rem .15rem;
    font-size: @font-size-s;
    color: @color-font-gray;
    .books-item {
      overflow: hidden;
      box-sizing: border-box;
      /*padding-left: 30%;*/
      position: relative;
      font-size: @font-size-s;
      margin-bottom: .2rem;
      line-height: 2;
      cursor: pointer;
      img {
        float: left;
        width: 40%;
        margin-right: 3%;
      }
      h3 {
        font-size: @font-size-l;
        color:@color-theme-font;
        .nowrap;
      }
      h4 {
        .nowrap;
      }
      .intro{
        position: absolute;
        bottom: 0;
        left: 43%;
        p {
          .nowrap;
        }
      }
    }
  }
</style>



