<template>
  <div class="book-shop">
    <search></search>
    <div class="top-slider-wrap">
      <swiper :options="options" ref="swiper1" :needIntro="needIntro" class="top-slider-1" :sliders="sliderData"
      :newClass="sliderClassName"> </swiper>
    </div>
    <div class="shop-nav"></div>
    
    <!-- <swiper :></swiper> -->

    <panel :datas="moduleMan" :title="moduleManTitle"></panel>
    <panel :datas="moduleFeman" :title="moduleFemanTitle"></panel>
    <panel :datas="moduleRecommend" :title="moduleRecommendTitle"></panel>
    <panel :datas="modulePlusRecommend" :title="modulePlusRecommendTitle"></panel>
    <sort :datas="sort"></sort>
  </div>
</template>

<script>
import Swiper from "components/base/Swiper"
import Panel from "components/base/Panel"
import Search from "components/base/Search"
import Sort from "./Sort"
import {getSliderList, getModules,getSort} from "api/bookshop"
import {getBooks} from "common/js/class"
import {ERR_OK} from "common/js/config"

export default {
  name: 'book-shop',
  components: {
    Swiper,
    Panel,
    Sort,
    Search
  },
  created() {
    // console.log(this.sliderData)
  },
  mounted(){
    this._getSliders();
    this._getModules();
    this._getSort();

  
  },
  methods: {
    _getSliders() {
      getSliderList().then(res => {
        if (res.status == ERR_OK) {
          let data = res.data.data.module1;
          data.forEach(item => {
            this.sliderData.push(getBooks(item))
          })
        }
      })
    },
    _getModules() {
      getModules().then(res => {
        if (res.status == ERR_OK) {
          let data = res.data.data;
          data.module2.forEach((item, index) => {
            this.moduleMan.push(getBooks(item));
            this.moduleFeman.push(getBooks(data.module3[index]));
            this.moduleRecommend.push(getBooks(data.module4[index]));
            this.modulePlusRecommend.push(getBooks(data.module5[index]));
          })
        }
      })
    },
    _getSort(){
      getSort().then(res=>{
        if(res.status==ERR_OK){
          let data=res.data.show.module6.class_data;
          for(let key in data){
            if(data.hasOwnProperty(key)){
              let ary=[];
              ary.id=key;
              ary.name=data[key]["name"];
              ary.num=data[key]["num"];
              this.sort.push(ary);
              ary=null;
            }
          }
        }
      })
    },
  },
  computed:{
  options(){
    return{
      autoplay:true,
      pagination:{
        el:".swiper-pagination"
      }
    }
  }
},
data() {
  return {
    topSliderNeedIntro: false,
    sliderData: [],
    moduleManTitle: "男频榜单",
    moduleFemanTitle: "女频榜单",
    moduleRecommendTitle: "分类推荐榜",
    modulePlusRecommendTitle: "强力推荐榜",
    moduleMan: [],
    moduleFeman: [],
    moduleRecommend: [],
    modulePlusRecommend: [],
    sliderClassName: "book-shop-top-sliders",
    sliderClassName2: "book-shop-top-sliders2",
    sort:[],
    needIntro:true,
  }
}
}
</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
.book-shop{
  padding-bottom: .97rem;
}
.top-slider {
  margin: .2rem 0;
}

.swiper-slide-href {
  display: block;
  background: url("~common/img/logo.png");
  .swiper-slide-href-img {
    display: block;
    width: 100%;
  }
}
.top-slider-wrap{
  height: 4rem;
  position: relative;
  background: url("~common/img/bannerbg.jpg") no-repeat center center;
  background-size:cover;
}
.shop-nav{
  margin:.2rem 0;
  height: 1rem;
  background:url("~common/img/bookshef-nav.jpg") no-repeat center center;
  background-size: cover;
}
</style>



