import {bookKey} from "./config";
import {getTime} from "../common/js/js";
import axios from "axios"
import md5 from "js-md5";
import {formatUrl} from "../common/js/jsonp";

export function getBookIntro(id) {
  let url="https://ognv1.sqreader.com/index.php?r=pcapi/pcbook/bookinfo",
    data=new FormData,
    time=getTime();
  data.append("bid",id);
  data.append("timestamp",time);
  data.append("sign",md5(id+time+bookKey));
  return axios.post(url,data);
}
export function getComment(authorId,bookId,page=0) {

  let url="https://read.xiaoshuo1-sm.com/novel/i.php",
    data={
    do:"sp_get",
      authorId:authorId,
      bookId:bookId,
      fetch:"top",
      source:"store",
      sqUid:8000000,
      page:page
    };
  data=formatUrl(data);
  url=url+url.indexOf("?")>0?data:("?"+data);
  return axios.get(url);
}
export function getCatalog(id) {
  let url="https://ognv1.sqreader.com/index.php?r=pcapi/pcbook/volumechapterlist",
    data=new FormData,
    time=getTime();
  data.append("bid",id);
  data.append("timestamp",time);
  data.append("sign",md5(id+time+bookKey));
  return axios.post(url,data);
}



