<template>
  <transition name="translate">
    <div class="book-search" @scroll="downloadMore" ref="book">
      <router-view></router-view>
      <back></back>
      <!--<search></search>-->
      <i class="icon-fanhuidingbu" v-show="isLong" @click="toTop"></i>
      <book-list :datas="books"></book-list>
      <loading :isLoading="isLoading"></loading>
    </div>
  </transition>
</template>

<script>
  import BookList from "components/base/BookList"
  import Loading from "components/base/Loading"
  import Back from "components/base/Back"
  import Search from "components/base/Search"
  import {getBookSearchList} from "api/booksearch"
  import {getBooks2} from "common/js/class"
  import {mapGetters, mapMutations} from "vuex"
  import {ERR_EMPTYKEYWORD, ERR_OK} from "common/js/config"
  import {getBJtime} from "common/js/js"

  export default {
    name: 'search',
    components: {
      BookList,
      Loading,
      Back,
      Search,
    },
    created() {
      this._getBookSearchList()
    },
    methods: {
      toTop() {
        let totalL = this.$refs.bookSort.scrollTop;
        let timer = setInterval(() => {
          if (totalL - 40 < 0) {
            totalL = 0;
            clearInterval(timer)
          } else {
            totalL -= 40;
          }
          this.$refs.bookSort.scrollTo(0, totalL);

        }, 20)
      },
      _getBookSearchList() {
        getBookSearchList(this.page, this.pageSize, this.classId, this.gender, this.end, this.chargeType, this.keyword).then(res => {
            if (res.status == ERR_OK) {
              if (res.data.state == ERR_EMPTYKEYWORD) {
                this.isLoading = 1;
                return;

              }
            }
            if (this.page == 1) {
              this.books = [];
            }
            let data = res.data.data;
            this.isLoading = this.page * this.pageSize >= res.data.total ? 1 : 0;
            data.forEach(item => {
              item.up_time=getBJtime(item.up_time)
              item.book_name = item.book_name.replace(new RegExp(this.keyword, "g"), (...arg) => {
                return `<span style="color:#eb634b">${arg[0]}</span>`
              })
              this.books.push(getBooks2(item))
            })
          }
        )
      },
      downloadMore() {
        if (this.on) {
          let el = this.$refs.book,
            h = el.offsetHeight,
            scrollHeight = el.scrollHeight,
            scrollTop = el.scrollTop;
          if (h <= scrollTop) {
            this.isLong = true;
          } else {
            this.isLong = false;
          }
          if (h + scrollTop >= scrollHeight) {
            this.on = false;
            this.set_page(this.page + 1)
            this._getBookSearchList();
            this.timer = setTimeout(() => {
              clearTimeout(this.timer);
              this.on = true;
            }, 3000)
          }
        }
      },
      ...mapMutations({
        "set_page": "SET_PAGE"
      })
    },
    computed: {
      ...mapGetters([
        "page",
        "classId",
        "gender",
        "end",
        "chargeType",
        "keyword",
      ])
    },
    data() {
      return {
        books: [],
        isLoading: 0,
        pageSize: 9,
        isLong: false,
        on: true,
      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";

  .book-search {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 5556;
    overflow: auto;
    background-color: @color-bg-white;
  }
</style>



