<template>

  <ul>
    <li v-for="item in onePageC" class="cata-list" @click="toReader(item)">
      {{item.chap_name}}
    </li>
    <li class="load-more" v-if="isAll" @click="loadMore">继续加载...</li>
    <li class="load-more" v-else>加载完毕</li>
  </ul>
</template>

<script>
  import {getCatalog} from "api/bookintro"
  import {mapGetters, mapMutations} from "vuex"
  import {ERR_OK, ERR_EMPTY} from "common/js/config"

  export default {
    name: 'catalog',
    created() {
      this._getCatalog();
    },
    mounted() {

    },
    methods: {
      loadMore() {
        this.onePageC = this.onePageC.concat(this.catalog.slice(this.page * 20, (this.page + 1) * 20))
        this.page++;
      },
      _getCatalog() {
        getCatalog(this.bookId).then(res => {
          if (res.status == ERR_OK) {
            this.catalog = [];
            this.page = 1;
            let data = res.data.data;
            data.forEach(item => {
              item.volumeList.chapters.forEach(i => {
                i.chap_name = i.chap_name.replace(/\s/g, "");
                this.catalog = this.catalog.concat(i);
              })
            })
            this.onePageC = this.catalog.length > 20 ? this.catalog.slice(0, 20) : this.catalog;
          }
        })
      },
      toReader(item) {
        this.$emit("startRead", item.chapid)
      },
      ...mapMutations({
        "set_chapId": "SET_CHAPID",
        "set_bookId": "SET_BOOKID"
      })
    },
    computed: {
      isAll() {
        return (Math.ceil(this.catalog.length / 20) - this.page) > 0;
      },
      ...mapGetters([
        "bookId"
      ])
    },
    data() {
      return {
        catalog: [],
        onePageC: [],
        page: 1,
      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";

  .cata-list {
    line-height: 2;
    color: @color-theme-font;
    font-size: @font-size-s;
    color: @color-theme;
    cursor: pointer;
    margin: 0 .2rem;
    border-bottom: 1px dashed @color-line;

  }

  .load-more {
    font-size: @font-size-m;
    text-align: center;
    font-weight: 700;
    line-height: 2;
    color: @color-theme;
    cursor: pointer;

  }
</style>



