<template>
  <div class="search-form">
  <i class="icon-fangdajing" @click="toSearch=!toSearch"></i>
    <input :class="{active: !toSearch}" v-model="searchText" @keyup.enter="search" type="text" class="search-text">

  </div>
</template>

<script>
  import {mapActions,mapGetters} from "vuex"
    export default {
        name: 'search',
      computed:{
        ...mapGetters(["keyword"])
      },
      methods:{
        search(){
          this.selectKeyWord({keyword:`${this.searchText}`});
          this.$router.push({path:`/booksearch/${encodeURIComponent(this.searchText)}`});
        },
        ...mapActions({
          "selectKeyWord":"selectKeyWord"
        })
      },
        data() {
            return {
              searchText:"",
              toSearch:false,
            }
        }
    }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  .search-form{
    position: absolute;
   left: 0;
   right: 0;
    padding-right: .4rem;
    overflow: hidden;
    z-index: 555;
    margin-right:.1rem;
    color:@color-font-white;
    .icon-fangdajing{
      position: absolute;
      right: .2rem;
      top: 50%;
      margin-top: -.16rem;
      font-size: @font-size-xl;
cursor:pointer;
    }
    .search-text{
      font-size: @font-size-m;
      height: .5rem;
      box-sizing: border-box;
      float: right;
      margin: .3em 0;
      margin-right:.3rem;
      line-height: 1.5;
      border-radius: .2rem;
      outline: none;
      width: 3rem;
      padding: 0 .1rem;
      border:1px solid @color-font-gray;
      transition: all .7s;
      opacity:1;
      &.active{
        width:0;
        opacity: 0;
      }
    }
  }
  .translate-enter-active, .translate-fade-active {
    transition: all .7s
  }

  .translate-enter, .translate-leave-to {
    transform: translateY(100%);
  }
</style>



