const jsonPorigin = require("jsonp");
const ops = {param: 'jsonpCallback'};

export function formatUrl(data) {
  let str = "";
  for (var key in data) {
    if (data.hasOwnProperty(key)) {
      let value = data[key] == undefined ? "" : data[key];
      str += "&" + key + "=" + value;
    }
  }
  return str.substring(1)
}

export function jsonp(url, data) {
  url += url.indexOf("?") > 0 ? formatUrl(data) : "?" + data;
  return new Promise((res, rej) => {
    jsonPorigin(url, ops, (err, data) => {
      if (!err) {
        res(data);
      } else {
        rej(err);
      }
    })
  })
}



