<template>
  <div class="back">
    <i class="icon-fanhui" @click="comeback"></i>
  </div>
</template>

<script>
    export default {
        name: 'back',
        methods:{
          comeback(){
            this.$router.back();
          },
        },
        data() {
            return {}
        }
    }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  .back {
    height: .8rem;
    background-color: @color-theme;
    .icon-fanhui {
      display: block;
      color: @color-bg-white;
      font-size: @font-size-l;
      line-height: .8rem;
      padding-left: .3rem;
      cursor: pointer;

    }
  }
</style>



