import {getTime} from "./js";

let storage = window.localStorage,
  key = "rd_books",
  light = "liangdu";

export function addBooks(id, v) {
  let ary = JSON.parse(storage.getItem(key)) || [];
  if ((typeof id ).toLowerCase()=== "object") {
    if (ary.some(item => item.id == id.id)) {
      ary = ary.filter(item => item.id != id.id);
    }
    ary.push(id);
    storage.setItem(key, JSON.stringify(ary));
    return;
  }
  if ((typeof id ).toLowerCase()=== "string" && v) {
    ary.forEach(item => {
      if (item.id == id) {
        item.cid = v;
        item.time=getTime();
      }
    })
    storage.setItem(key, JSON.stringify(ary));
    return;
  }
}

export function removeBooks(val) {
  let ary = JSON.parse(storage.getItem(key)) || [];
  console.log(ary,val);
  ary = ary.filter(item => item.cid != val);
  storage.setItem(key, JSON.stringify(ary))
}

export function hasBooks(id) {
  let ary = JSON.parse(storage.getItem(key)) || [];
  ary = ary.some(item => item.id == id);
  return ary
}
export function clearBooks() {
  storage.removeItem(key);
}

export function getBooks(id) {
  let ary = JSON.parse(storage.getItem(key)) || [];
  if(id!=undefined){
    ary = ary.filter(item => item.id == id)[0];
  }
  return ary;
}
export function getLight() {
  return JSON.parse(storage.getItem(light)) || "0";
}
export function setLight(num) {
  storage.setItem(light, num)
}
export function getTheme() {
  return JSON.parse(storage.getItem("theme")) || "0";
}
export function setTheme(type) {
  storage.setItem("theme",type)
}
export function getFontSize() {
  return JSON.parse(storage.getItem("fontsize")) || "18";
}
export function setFontSize(type) {
  storage.setItem("fontsize",type)
}

export function getFontFamily() {
  return JSON.parse(storage.getItem("fontfamily")) || "0";
}
export function setFontFamily(type) {
  storage.setItem("fontfamily",type)
}




