import {bookKey} from "./config";
import {getTime} from "../common/js/js";
import axios from "axios"
import md5 from "js-md5";

export function getBookSearchList(page,pageSize,classId,gender,end,chargeType,keyword) {
  let url="https://ognv1.sqreader.com/index.php?r=pcapi/pcbook/search",
    data=new FormData,
    time=getTime();
  data.append("page",page);
  data.append("pageSize",pageSize);
  data.append("timestamp",time);
  data.append("classId",classId);
  data.append("end",end);
  data.append("gender",gender);
  data.append("chargeType",chargeType);
  data.append("keyword",keyword);
  data.append("sign",md5(keyword+time+bookKey));
  return axios.post(url,data);
}



