<template>
  <div>
    <div class="loader" v-if="isLoading==0">Loading...</div>
    <div class="loaded" v-else-if="isLoading==1">已经加载完毕</div>
    <div class="no-fiction" v-else>没有满足条件的小说</div>
  </div>
</template>

<script>
  export default {
    name: 'loading',
    props: {
      isLoading: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {}
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";

  .loaded, .no-fiction {
    line-height: 2;
    width: auto;
    margin: 0 auto;
    text-align: center;
    font-family: helvetica, arial, sans-serif;
    position: relative;
    text-transform: uppercase;
    font-size: @font-size-l;
    font-weight: 900;
    color: @color-theme;
    letter-spacing: 0.2em;

  }

  .loader {

    .loaded;
    width: 2.5rem;
    font-size: @font-size-l;
    &::before, &::after {
      content: "";
      display: block;
      width: .15rem;
      height: .15rem;
      background: @color-theme;
      position: absolute;
      animation: load .7s infinite alternate ease-in-out;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }
  }

  @keyframes load {
    0% {
      left: 0;
      height: .3rem;
      width: .15rem
    }
    50% {
      height: .08rem;
      width: .4rem
    }
    100% {
      left: 2.35rem;
      height: .3rem;
      width: .15rem
    }
  }
</style>



